import { motion, useScroll, useTransform } from "framer-motion";
import { useRef } from "react";

export default function Home4sections() {
  const ref = useRef(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["start end", "end end"],
  });

  // Lijn animaties (vw units)
  const line1Height = useTransform(scrollYProgress, [0, 1], ["0vw", "35vw"]);
  const line2Height = useTransform(scrollYProgress, [0, 1], ["0vw", "40vw"]);
  const line3Height = useTransform(scrollYProgress, [0, 1], ["0vw", "35vw"]);
  const line4Height = useTransform(scrollYProgress, [0, 1], ["0vw", "40vw"]);

  // Tekst opacity animaties (start bij 60%, volledig zichtbaar bij 80%)
  const text1Opacity = useTransform(scrollYProgress, [0.6, 0.8], [0, 1]);
  const text2Opacity = useTransform(scrollYProgress, [0.6, 0.8], [0, 1]);
  const text3Opacity = useTransform(scrollYProgress, [0.6, 0.8], [0, 1]);
  const text4Opacity = useTransform(scrollYProgress, [0.6, 0.8], [0, 1]);

  return (
    <div
      ref={ref}
      className="relative w-full min-h-[100vw] flex items-end"
      style={{ paddingBottom: "5vw" }}
    >
      <div
        className="w-full"
        style={{ paddingLeft: "4vw", paddingRight: "4vw" }}
      >
        <div className="flex" style={{ gap: "8vw" }}>
          {/* Sectie 1 */}
          <div className="flex items-end">
            <motion.div
              style={{
                width: "1vw",
                height: line1Height,
                backgroundColor: "black",
              }}
            />
            <motion.p
              style={{
                opacity: text1Opacity,
                marginLeft: "4vw",
                fontSize: "1.5vw",
              }}
            >
              Tekst 1
            </motion.p>
          </div>

          {/* Sectie 2 */}
          <div className="flex items-end">
            <motion.div
              style={{
                width: "1vw",
                height: line2Height,
                backgroundColor: "black",
              }}
            />
            <motion.p
              style={{
                opacity: text2Opacity,
                marginLeft: "4vw",
                fontSize: "1.5vw",
              }}
            >
              Tekst 2
            </motion.p>
          </div>

          {/* Sectie 3 */}
          <div className="flex items-end">
            <motion.div
              style={{
                width: "1vw",
                height: line3Height,
                backgroundColor: "black",
              }}
            />
            <motion.p
              style={{
                opacity: text3Opacity,
                marginLeft: "4vw",
                fontSize: "1.5vw",
              }}
            >
              Tekst 3
            </motion.p>
          </div>

          {/* Sectie 4 */}
          <div className="flex items-end">
            <motion.div
              style={{
                width: "1vw",
                height: line4Height,
                backgroundColor: "black",
              }}
            />
            <motion.p
              style={{
                opacity: text4Opacity,
                marginLeft: "4vw",
                fontSize: "1.5vw",
              }}
            >
              Tekst 4
            </motion.p>
          </div>
        </div>
      </div>
    </div>
  );
}
